body {
  margin: 0;
  padding: 0;
  background-color: #323232;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 1em;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #f0f0f0;
}
h1,
h2,
h3 {
  font-family: "IBM Plex Serif", serif;
  font-size: 0.875em;
  font-weight: 400;
  color: #e1e1e1;
}
h1 {
  margin: 8px;
  text-align: center;
}
p {
  font-size: 0.875em;
  font-weight: 400;
  color: #c8c8c8;
}
ul {
  list-style-type: none;
}
a {
  color: #64cdee;
  text-decoration: none;
  font-size: 0.875em;
  cursor: pointer;
}
.navigator {
  position: fixed;
  height: 28px;
  width: 100vw;
  bottom: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background-color: rgba(50, 50, 50, 0.5);
}
.navigator .navs {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  height: 24px;
  max-width: 50%;
  overflow: hidden;
  margin: 2px;
  cursor: pointer;
}
.navigator .tools {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 24px;
  width: 50%;
  margin: 2px;
}
.navigator .icon {
  cursor: pointer;
}
.navigator .disabled {
  opacity: 0.125;
  cursor: not-allowed;
}
.navigator .gallery-name {
  height: 24px;
  line-height: 24px;
  font-size: 0.875em;
  font-weight: 600;
}
.gallery-list li {
  margin: 8px;
}
.gallery-list li .title {
  color: #f0f0f0;
  text-align: center;
  height: 20px;
  line-height: 20px;
}
.photo {
  max-width: 100%;
  display: block;
  outline: 1px solid #fafafa;
  outline-offset: -0.5px;
}
.single-photo {
  text-align: center;
  margin: 8px 4px 32px 4px;
}
.single-photo .photo {
  max-height: 75vh;
  display: block;
  margin: 0 auto;
  -webkit-box-shadow: 0 32px 64px rgba(0, 0, 0, 0.5);
          box-shadow: 0 32px 64px rgba(0, 0, 0, 0.5);
}
.single-photo p {
  margin: 8px 0;
}
.single-gallery {
  text-align: center;
  margin: 8px 4px 32px 4px;
}
.single-gallery .photo {
  display: block;
  margin: 8px auto;
  -webkit-box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
          box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}
.single-gallery p {
  margin: 8px 0;
}
